import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)


export default new Vuex.Store({
  state: {
    //
    carts:[],
    //
    userInfo:null,
    // 
    showFooterNav:true,
    //id
    cate_detail_id:0,
    //
    cate_detail_list:[],
    //goods_id
    checkedGoodsIdArr:[]
  },
  mutations: {
    //
    addCart(state,payload){
      //carts[{goods_id:1,count:1}]
      const index = state.carts.findIndex(item=>item.goods_id==payload.goods_id)
      if(index===-1){//
        state.carts.push(payload)
      }else{
        state.carts[index].count += payload.count;
      }
    },
    //
    setUserInfo(state,user){
      state.userInfo = user;
    },
    //
    removeUserInfo(state){
      state.userInfo = {}
    },
    //id
    set_cate_detail_id(state,payload){
      state.cate_detail_id = payload
    },
    //
    setShowFooterNav(state,payload){
      state.showFooterNav = payload
    },
    //
    clearCart(state){
      state.carts=[]
      state.checkedGoodsIdArr=[]
    }
  },
  actions: {
    //
    addCart(context,payload){
      context.commit('addCart',payload)
    },
    // 
    async getUserInfo(context){
      //axios get
      // console.log('')
      const { data: res } = await axios.get("/user", {
        headers: { Authorization: window.sessionStorage.getItem('token') },
      });
      console.log(res)
      if(res.status==='ok'){
        // 
        console.log("",res.data)
        res.data.avatar = require("@/" + res.data.avatar);
        context.commit('setUserInfo',res.data)
        return true;
      }else{
        console.log("")
        return false
      }
    },
  }
})
