<template>
  <div class="order">
    <!--  -->
    <div class="top">
      <div class="back-to-cart" @click="backToCart">&lt;</div>
      <div class="title"></div>
    </div>
    <!--  -->
    <div class="address-area">
      <p class="address">
        <van-form @submit="onSubmit" ref="orderFormRef">
          <van-field
            v-model="fieldValue"
            is-link
            readonly
            label=""
            placeholder=""
            @click="show = true"
            :rules="[{ required: true }]"
          />
          <!--  -->
          <van-popup v-model="show" round position="bottom">
            <van-cascader
              v-model="cascaderValue"
              title=""
              :options="options"
              @close="show = false"
              @finish="onFinish"
            />
          </van-popup>
          <van-field
            v-model="addressValue"
            label=""
            placeholder=""
            :rules="[{ required: true }]"
          />
          <van-field
            v-model="phone"
            label=""
            placeholder=""
            :rules="[
              {
                required: true,
                pattern: phonePattern,
                message: '',
              },
            ]"
          />
          <van-field
            v-model="name"
            label=""
            placeholder=""
            :rules="[{ required: true }]"
          />
        </van-form>
      </p>
    </div>

    <!--  -->
    <div class="check-cart">
      <div class="shops-name"></div>
      <div class="imgList">
        <div class="img">
          <img
            :src="goods.src"
            alt=""
            v-for="(goods, index) in checkGoodsArr"
            :key="index"
          />
        </div>
        <div class="allCount">{{ checkedGoodsCount }}</div>
      </div>
      <div class="delivery">
        <div class="title"></div>
        <div class="way">
          <p></p>
          <p></p>
          <p></p>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="bottom">
      <div class="cost">{{ totalCost }}</div>
      <div class="submit-order" @click="submit_order"></div>
    </div>
  </div>
</template>

<script>
import qs from "qs";
export default {
  data() {
    return {
      show: false,
      fieldValue: "",
      cascaderValue: "",
      addressValue: "",
      phone: "",
      name: "",
      phonePattern: /\d{11}/,
      // children 
      options: [
        {
          text: "",
          value: "330000",
          children: [{ text: "", value: "330100" }],
        },
        {
          text: "",
          value: "320000",
          children: [{ text: "", value: "320100" }],
        },
        {
          text: "",
          value: "310000",
          children: [
            {
              text: "",
              value: "310100",
              children: [{ text: "", value: "320101" }],
            },
          ],
        },
      ],
    };
  },
  computed: {
    //
    checkGoodsArr() {
      return this.$store.state.carts.filter((goods) =>
        this.$store.state.checkedGoodsIdArr.includes(goods.goods_id)
      );
    },
    //
    totalCost() {
      const cost = this.checkGoodsArr.reduce((pre, cur) => {
        return pre + cur.count * cur.price;
      }, 0);
      return cost;
    },
    // 
    checkedGoodsCount() {
      return this.checkGoodsArr.reduce((pre, cur) => {
        return pre + cur.count;
      }, 0);
    },
  },
  methods: {
    backToCart() {
      this.$router.push("/cart");
    },
    //
    async submit_order() {
      this.$refs.orderFormRef.submit();
    },
    //  finish 
    onFinish({ selectedOptions }) {
      this.show = false;
      this.fieldValue = selectedOptions.map((option) => option.text).join("/");
    },
    // 
    async onSubmit() {
      const address = this.fieldValue + "/" + this.addressValue;
      const checkedGoods = this.checkGoodsArr.map((good) => {
        return {
          goods_id: good.goods_id,
          count: good.count,
        };
      });
      //   
      const { data: res } = await this.http.post(
        "/order",
        qs.stringify({
          checkedGoods: JSON.stringify(checkedGoods),
          address: address,
          phone: this.phone,
          name: this.name,
        }),
        {
          headers: { Authorization: window.sessionStorage.getItem("token") },
        }
      );
      if (res.status === "ok") {
        this.$toast({
          message: "",
          onClose: () => {
            //
            this.$store.commit('clearCart')
            this.$router.push("/cart");
          },
        });
      }
      console.log("order", res);
    },
  },
};
</script>

<style lang="less" scoped>
.order {
  width: 100%;

  // 
  .top {
    height: 0.45rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    background-color: #ffffff;
    .back-to-cart {
      font-size: 24px;
      margin-left: 0.2rem;
    }
    .title {
      width: 75%;
      text-align: center;
      font-size: 20px;
    }
  }
  // 
  .address-area {
    padding: 0 10px 10px 10px;
    border-bottom: 2px dashed #cccccc;
    background-color: #ffffff;
  }
  //
  .check-cart {
    margin-top: 10px;
    padding: 10px;
    background-color: #ffffff;
    .shops-name {
      font-size: 14px;
    }
    //
    .imgList {
      margin-top: 10px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .img {
        width: 80%;
        height: 1rem;
        overflow: auto;
        display: flex;
        flex-wrap: nowrap;
        img {
          width: 1rem;
        }
      }
      // 
      .img::-webkit-scrollbar {
        display: none !important;
        width: 0;
        height: 0;
      }
    }
    .delivery {
      display: flex;
      justify-content: space-between;
      margin-top: 10px;
      .way {
        font-size: 0.12rem;
        // display: flex;
        // justify-content: flex-start;
        // align-items: flex-start;
        // flex-direction: column;
        p {
          text-align: right;
          margin: 0;
        }
      }
    }
  }
  // 
  .bottom {
    width: 100%;
    height: 0.5rem;
    position: fixed;
    left: 0;
    bottom: 0;
    background: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    .cost {
      margin-left: 0.2rem;
      color: rgb(194, 5, 5);
    }
    .submit-order {
      width: 0.8rem;
      height: 0.4rem;
      line-height: 0.4rem;
      text-align: center;
      color: #ffffff;
      font-size: 0.14rem;
      border-radius: 0.2rem;
      margin-right: 0.2rem;
      background: linear-gradient(to right, rgb(216, 6, 6), rgb(238, 111, 7));
    }
  }
}
</style>